import React from 'react';
import { TXCStatus } from './Utils.js';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem } from 'reactstrap';

class NavBar extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleDropdown = this.toggleDropdown.bind(this);

    this.state = {
      isOpen: false,
      dropdownOpen: false
    };
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  toggleDropdown() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  render() {
    const hasWeb3 = this.props.addressList[0] != undefined;
    const web3StatusClass = "fa" + (hasWeb3 ? "s" : "r") +
      " fa-circle text-" + (hasWeb3 ? "success" : "danger");

    return (
      <div>
        <Navbar className="text-monospace bg-light" card light expand="md">
          <img src={this.props.logo} width="30" height="30" className="d-inline-block align-top ml-4 mr-2" alt="txcast logo"/>
          <NavbarBrand className="font-weight-bold" style={{"letterSpacing":"2px"}}>
            TXCast<sub><small className="text-weight-light font-italic">beta</small></sub>
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem className="mr-3">
                <NavLink
                  data-tip="Under construction"
                  className="text-primary"
                >
                  Help
                </NavLink>
              </NavItem>
              <NavItem
                className="mr-3"
              >
                <TXCStatus
                  balance={this.props.txs}
                  allowance={this.props.allowance}
                  active={this.props.allowance  + this.props.txs > -1}
                />
              </NavItem>
              <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggleDropdown} nav inNavbar>
                <DropdownToggle nav caret className="text-black">
                  Address and Blocks
                </DropdownToggle>
                <DropdownMenu right flip={false}>
                  <DropdownItem
                    target="_blank"
                    href={this.props.ethsUrl}
                  >
                    <small className="float-right">
                      <i style={{"color": this.props.netID === "1" ? "teal": "purple"}} className="fas fa-wifi mr-2"></i>
                      TXCast Address: {this.props.ethsAddress.slice(0,6) + ".." + this.props.ethsAddress.slice(-4)}
                    </small>
                  </DropdownItem>
                  {this.props.addressList[0] && <AddressItem {...this.props}/>}
                  <DropdownItem style={{"borderTopColor":"gray"}} divider/>
                  <DropdownItem disabled>
                    <small className="float-left">
                      <i className={web3StatusClass}></i> web3 {hasWeb3 ? "enabled" : "disabled"}
                    </small>
                    <small className="float-right">
                      Block: <span className="text-monospace">{this.props.blockNumber}</span>
                    </small>
                  </DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}

const AddressItem = (props) => (
  <DropdownItem disabled> 
    <small className="float-right">
      Wallet: {props.addressList[0].slice(0,6) + ".." + props.addressList[0].slice(-4)}
    </small>
  </DropdownItem>
);
export default NavBar;
