import React, { Component } from 'react';
import { CollapseButton } from './Utils.js';
import withCollapse from './withCollapse.js';

import { FilePond, registerPlugin } from 'react-filepond';
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type';

import 'filepond/dist/filepond.min.css';

registerPlugin(FilePondPluginFileValidateType)

class SendBundleContainer extends Component {
  constructor(props){
    super(props);
    this.state = {callDataTX: {}};
  }

  render() {
    return (
      <div className="card text-white mb-4">
        <div className="card-header">
          <h4>
            Request Bundle TX <sub><small><i className="fas fa-exclamation-triangle"></i> Unstable Feature</small></sub>
            <CollapseButton
              onCollapse={this.props.onCollapse}
              collapsed={this.props.collapsed}
            />
          </h4>
        </div>  
        <div className={"card-body" + (this.props.collapsed ? " card-collapse" : "")}>
          <div>
            <FilePond
              dropValidation
              acceptedFileTypes={['text/csv']}
              onaddfile={(e,f) => {
                const Papa = require("papaparse/papaparse.min.js");
                Papa.parse(f.file, {
                  skipEmptyLines: 'greedy',
                  complete: this.props.onBundle 
                });
              }}
              server={{
                method: 'GET',
                process: {
                  url: '.',
                  method: 'OPTIONS',
                }
              }}
            />
          </div>
        </div>
      </div>
    );
  }

  onSendBundle(e) {
    e.preventDefault();
    this.props.onSendBundle(this.state);
  }

  onInputChange(newState) {
    this.setState(newState);
  }
}

export default withCollapse(SendBundleContainer);
