import React, { Component } from 'react';
import { Footer, PatternBg, ActionModal } from './Utils.js';
import Web3 from 'web3';
import ReactTooltip from 'react-tooltip';
import axios from 'axios';
import NavBar from './NavBar.js';
import Modals from './Modals.js';
import { ToastContainer, toast } from 'react-toastify';
import * as handlers from './web3handlers.js';

import logo from './img/SVG/logo.svg';
import './App.css';
import 'react-toastify/dist/ReactToastify.min.css';

import TXQueueContainer from './TXQueueContainer.js';
import SchedulerContainer from './SchedulerContainer.js';
import TXCContainer from './TXCContainer.js';
import NoticesContainer from './NoticesContainer.js';
import DepositContainer from './DepositContainer.js';
import BalanceContainer from './BalanceContainer.js';
import SendRawContainer from './SendRawContainer.js';
import SendBundleContainer from './SendBundleContainer.js';


class App extends Component {
  constructor(){
    super();

    this.state = {
      addressList : [],
      queue : {},
      blockNumber : 0,
      balance: 0,
      netID: "1",
      hasTXS: false,
      blocksFormat: true,
      server: 'prod',
      modal: false,
    }

    this.Modals = Modals;

    this.web3 = new Web3(Web3.givenProvider || "http://localhost:8545");

    this.mew = "https://www.myetherwallet.com/signmsg.html";

    this.addresses = {
      "42": {
        eths: "0xa0475d202be30019e56306cc08f8998887135258",
        eths_old: "0xe49bb13f1e57de8364586bbb82bf79d0c8aac074",
        txssale: "0x2ffbee1b7eda9d6187f4f875899c9f8f28d927ed",
        txs: "0x12C942fDbE9981E68DC153CC92dA2e2c301F5a9A",
      },
      "1": {
        eths: "0x0de3f0cd422e25d1c20fc8124e8bd0181e68fa30",
        eths_old: "0xf99E87098f4F1C8d8bd6a3df7181B3acdc21848A",
        txssale: "0x02e48bb0dbdc0ebbc041e725a12784239a2d10df",
        txs: "0x390e0070323df12b024617854f698cef5d309a93",
      }
    };

    this.urls = {
      'dev': "http://localhost:5000",
      'devrem': "http://txcast.io:5000",
      'prod': "https://api.txcast.io",
    }

    this.abiTypes = {
      eths: {
        withoutSignature: [
          'address', // target
          'uint256', // amount
          'uint256', // reward
          'bool',    // byBlock
          'uint256', // condition
          'uint256', // nonce
          'uint256', // netID
          'bool',    // USD 
        ],
        withSignature: [
          'address', // target
          'uint256', // amount
          'uint256', // reward
          'bool',    // byBlock
          'uint256', // condition
          'uint256', // nonce
          'uint256', // netID
          'bytes',   // signature 
        ],
        bundleWithoutSignature: [
          'address[]', // target
          'uint256[]', // amount
          'uint256',   // reward
          'bool',      // byBlock
          'uint256',   // condition
          'uint256',   // nonce
          'uint256',   // netID
          'bool',      // USD 
        ],
        bundleWithSignature: [
          'address[]', // target
          'uint256[]', // amount
          'uint256',   // reward
          'bool',      // byBlock
          'uint256',   // condition
          'uint256',   // nonce
          'uint256',   // netID
          'bytes',     // signature 
        ],
      }
    }

    this.toggle = this.toggle.bind(this);
    this.updateBlock = this.updateBlock.bind(this);

    this.cancelTX = handlers.cancelTX.bind(this);
    this.signTX = handlers.signTX.bind(this);
    this.sendTX = handlers.sendTX.bind(this);
    this.getTXS = handlers.getTXS.bind(this);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  } 

  async componentDidMount() {
    try {
      await window.ethereum.enable();

      const a = await this.web3.eth.getAccounts();
      const id = await this.web3.eth.net.getId();

      this.setState({
        addressList: a,
        netID: id
      }, () => {
        const address = a[0];
        this.getTXS(address);

        this.web3.currentProvider.publicConfigStore.on('update', (update) => {
          if(this.state.addressList[0].toLowerCase() !== update.selectedAddress)
            this.setState({
              addressList:[this.web3.utils.toChecksumAddress(update.selectedAddress)],
              netID:update.networkVersion.toString()
            }, () => {
              this.getTXS(this.state.addressList[0]);
            });
        });

        this.updateBlock();
        setInterval(this.updateBlock, 15000);
      });
    } catch (e) {
      console.error(e,"no web3");
    }
  }

  mountFinished() {
    this.fetchData();
    setInterval(() => this.fetchData(), 10000);
  }

  fetchData() {
    const req = {
      url: this.urls[this.state.server] + '/signature',
      method: 'GET',
    };

    axios(req)
      .then(ans => {
        const queue = ans.data.reduce((acc, v) => { acc[v[0]] = v[1]; return acc; }, {});
        this.setState({queue: queue});
      });
  }

  async updateBlock() {
    const latestBlock = await this.web3.eth.getBlock('latest');
    if(latestBlock !== null && /[0-9]*/.test(latestBlock)) this.setState({blockNumber: latestBlock.number});
  }

  render() {
    const netID = this.state.netID.toString();
    const ethsAddress = this.addresses[netID].eths;
    const ethsUrl = "https://" + (netID === "1" ? "www" : "kovan") + ".etherscan.io/address/" + ethsAddress;
    const coinbaseUrl = "https://" + (netID === "1" ? "www" : "kovan") + ".etherscan.io/address/" + this.state.addressList[0];

    const data = {
      netID,
      ethsAddress,
      ethsUrl,
      coinbaseUrl
    }

    const modal = this.state.modal ? (
      <ActionModal
        innerHTML={this.state.innerHTML}
        params={this.state.params}
        action={this.state.action}
        toggle={this.toggle}
        modal={this.state.modal}
      />) : "";

    return (
      <div>
        <PatternBg />
        <ReactTooltip />
        <ToastContainer />
        <NavBar
          {...this.state}
          {...data}
          logo={logo}
        />
        { modal } 
        <div className="container-fluid px-md-5 mt-4">
          <div className="row">
            <div
              className="col-lg-5"
            >
              <TXQueueContainer
                account={this.state.addressList[0]}
                queue={this.state.queue}
                blockNumber={this.state.blockNumber}
                onFulfill={handlers.onFulfill.bind(this)}
                onCancel={handlers.onCancel.bind(this)}
                mountFinished={this.mountFinished.bind(this)}
                fromWei={this.web3.utils.fromWei}
              />
              <SendRawContainer 
                onSendRaw={handlers.onSendRaw.bind(this)}
              />
              <SendBundleContainer 
                onBundle={handlers.onBundle.bind(this)}
              />
            </div>
            <div className="col-lg-7">
              <div className="row">
                <div
                  className="col-lg-7"
                >
                  <SchedulerContainer
                    onSign={handlers.onSign.bind(this)}
                  />
                  <TXCContainer
                    onBuy={handlers.onBuy.bind(this)}
                  />
                </div>
                <div
                  className="col-lg-5"
                >
                  <DepositContainer
                    address={this.state.addressList[0]}
                    onDeposit={handlers.onDeposit.bind(this)}
                  />
                  <BalanceContainer
                    address={this.state.addressList[0]}
                    balance={this.state.balance}
                    onBalance={handlers.onBalance.bind(this)}
                  />
                  <NoticesContainer/>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default App;
